import Button from "@mui/material/Button";
import List from "@mui/material/List";
import Stack from "@mui/material/Stack";
import Grid from "@mui/material/Grid";
import SortIcon from "@mui/icons-material/Sort";
import Chip from "@mui/material/Chip";
import IconButton from "@mui/material/IconButton";
import FilterAltIcon from "@mui/icons-material/FilterAlt";
import Container from "@mui/material/Container";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import ListItemText from "@mui/material/ListItemText";
import DialogTitle from "@mui/material/DialogTitle";
import DialogContent from "@mui/material/DialogContent";
import DialogActions from "@mui/material/DialogActions";
import Dialog from "@mui/material/Dialog";
import RadioGroup from "@mui/material/RadioGroup";
import Radio from "@mui/material/Radio";
import FormControlLabel from "@mui/material/FormControlLabel";
import Divider from "@mui/material/Divider";
import React, {
  ChangeEvent,
  Fragment,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import { graphql, PageProps, Link } from "gatsby";
import Layout from "../../components/layout";
import { CollegeDetailsProps } from "../../templates/college-details";
import { sortBy } from "lodash";

type DataProps = {
  allStrapiCollege: {
    nodes: CollegeDetailsProps[];
  };
  allStrapiCollegeType: {
    nodes: {
      name: string;
      id: string;
      college_type: {
        name: string;
        id: string;
      };
    }[];
  };
};

const filterOptions = [
  {
    label: "All Colleges",
    value: "0",
  },
];

const sortOptions = [
  {
    label: "Colleges Code - A to Z",
    value: "0",
  },
  {
    label: "Colleges Name - A to Z",
    value: "1",
  },
  {
    label: "Colleges Code - Z to A",
    value: "2",
  },
  {
    label: "Colleges Name - Z to A",
    value: "3",
  },
];

// markup
const Colleges = ({
  data: { allStrapiCollege, allStrapiCollegeType },
}: PageProps<DataProps>) => {
  const [searchText, setSearchText] = useState("");
  const [isOpenFilterDialog, setOpenFilterDialog] = useState(false);
  const [filterRadioOption, setFilterRadioOption] = useState(
    () => filterOptions[0]
  );
  const [filterOption, setFilterOption] = useState(() => filterOptions[0]);

  const [sortRadioOption, setSortRadioOption] = useState(() => sortOptions[0]);
  const [sortOption, setSortOption] = useState(() => sortOptions[0]);
  const [isOpenSortDialog, setOpenSortDialog] = useState(false);

  const radioFilterGroupRef = useRef<HTMLElement>(null);
  const radioSortGroupRef = useRef<HTMLElement>(null);

  useEffect(() => {
    allStrapiCollegeType.nodes.map((collegeType) => {
      filterOptions.push({
        label: collegeType.name,
        value: collegeType.id,
      });
    });
  }, [allStrapiCollegeType.nodes]);

  const handleClickListItem = useCallback(() => {
    setOpenFilterDialog(true);
  }, []);

  const handleClickSortListItem = useCallback(() => {
    setOpenSortDialog(true);
  }, []);

  const handleClose = useCallback(() => {
    setOpenFilterDialog(false);
  }, []);

  const handleSortClose = useCallback((newValue?: string) => {
    setOpenSortDialog(false);
  }, []);

  const handleChange = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      setSearchText(event.target.value);
    },
    [setSearchText]
  );

  const filteredBranches = useMemo(() => {
    let results;
    if (filterOption.value !== "0") {
      setSearchText("");
      results = allStrapiCollege.nodes.filter(
        (college) => college.college_type.id === filterOption.value
      );
    } else {
      // filter branch by name or code
      results = allStrapiCollege.nodes.filter((branch) => {
        return (
          branch.name.toLowerCase().includes(searchText.toLowerCase()) ||
          branch.code.toString().includes(searchText)
        );
      });
    }
    // sort results by name or code using sortOption.value using lodash sortby
    if (sortOption.value === "0") {
      results = sortBy(results, "code");
    } 
    else if (sortOption.value === "1") {
      results = sortBy(results, "name");
    }
    else if (sortOption.value === "2") {
      results = sortBy(results, "code").reverse();
    }
    else if (sortOption.value === "3") {
      results = sortBy(results, "name").reverse();
    }

    return results;
  }, [filterOption.value, allStrapiCollege.nodes, searchText, sortOption.value]);

  const handleEntering = useCallback(() => {
    if (radioFilterGroupRef.current != null) {
      radioFilterGroupRef.current.focus();
    }
  }, []);

  const handleSortEntering = useCallback(() => {
    if (radioSortGroupRef.current != null) {
      radioSortGroupRef.current.focus();
    }
  }, []);

  const handleCancel = useCallback(() => {
    handleClose();
    setFilterRadioOption(filterOption);
  }, [filterOption]);

  const handleOk = useCallback(() => {
    handleClose();
    setFilterOption(filterRadioOption);
  }, [filterRadioOption]);

  const handleSortCancel = useCallback(() => {
    handleSortClose();
    setSortRadioOption(sortOption);
  }, [sortOption]);

  const handleSortOk = useCallback(() => {
    handleSortClose();
    setSortOption(sortRadioOption);
  }, [sortRadioOption]);

  const handleFilterChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const selectedOption = filterOptions.find(
      (option) => option.value === event.target.value
    );
    if (selectedOption) {
      setFilterRadioOption(selectedOption);
    }
  };

  const handleSortChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const selectedOption = sortOptions.find(
      (option) => option.value === event.target.value
    );
    if (selectedOption) {
      setSortRadioOption(selectedOption);
    }
  };

  return (
    <Layout headerTitle="Colleges">
      <Box>
        <TextField
          id="standard-search"
          label="Search College Name or Code"
          type="search"
          variant="filled"
          value={searchText}
          onChange={handleChange}
          fullWidth
        />
      </Box>
      <Container>
        <Grid
          container
          direction="row"
          justifyContent="space-between"
          alignItems="center"
        >
          <Chip label={`${filteredBranches.length} Colleges Found`} size="small" />

          <Stack direction="row" spacing={2}>
            <IconButton aria-label="filter" onClick={handleClickListItem}>
              <FilterAltIcon color="secondary" />
            </IconButton>
            <IconButton aria-label="sort" onClick={handleClickSortListItem}>
              <SortIcon color="secondary" />
            </IconButton>
          </Stack>
        </Grid>
      </Container>
      <List>
        {filteredBranches.map((node) => {
          // const branchSlug = createSlug(node.name);
          return (
            <Fragment key={node.id}>
              <ListItem disablePadding>
                <ListItemButton component={Link} to={`/${node.code}`}>
                  <ListItemText
                    primary={`${node.name} - ${node.code}`}
                    secondary={node.city}
                  />
                </ListItemButton>
              </ListItem>
              <Divider />
            </Fragment>
          );
        })}
      </List>
      <Dialog
        sx={{ "& .MuiDialog-paper": { width: "80%", maxHeight: 435 } }}
        maxWidth="xs"
        TransitionProps={{ onEntering: handleEntering }}
        open={isOpenFilterDialog}
      >
        <DialogTitle>Filter</DialogTitle>
        <DialogContent dividers>
          <RadioGroup
            ref={radioFilterGroupRef}
            aria-label="filter"
            name="filter"
            value={filterRadioOption.value}
            onChange={handleFilterChange}
          >
            {filterOptions.map((option) => (
              <FormControlLabel
                value={option.value}
                key={option.value}
                control={<Radio />}
                label={option.label}
              />
            ))}
          </RadioGroup>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCancel}>
            Cancel
          </Button>
          <Button onClick={handleOk}>Apply</Button>
        </DialogActions>
      </Dialog>
      <Dialog
        sx={{ "& .MuiDialog-paper": { width: "80%", maxHeight: 435 } }}
        maxWidth="xs"
        TransitionProps={{ onEntering: handleSortEntering }}
        open={isOpenSortDialog}
      >
        <DialogTitle>Sort</DialogTitle>
        <DialogContent dividers>
          <RadioGroup
            ref={radioSortGroupRef}
            aria-label="sort"
            name="sort"
            value={sortRadioOption.value}
            onChange={handleSortChange}
          >
            {sortOptions.map((option) => (
              <FormControlLabel
                value={option.value}
                key={option.value}
                control={<Radio />}
                label={option.label}
              />
            ))}
          </RadioGroup>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleSortCancel}>
            Cancel
          </Button>
          <Button onClick={handleSortOk}>Apply</Button>
        </DialogActions>
      </Dialog>
    </Layout>
  );
};

export const query = graphql`
  query {
    allStrapiCollege(sort: { order: ASC, fields: name }) {
      nodes {
        name
        code
        city
        id
        college_type {
          name
          id
        }
      }
    }
    allStrapiCollegeType(sort: { fields: name, order: ASC }) {
      nodes {
        name
        id
      }
    }
  }
`;

export default Colleges;
